
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import service from "@/uilt/request";
import { orderList, recOrder } from "@/api/order";
export default {
components: {
Panel,
Group,
Radio,
Confirm
},
watch: {
type: function(old, news) {
this.getData();
}
},
methods: {
onImgError(item, $event) {
console.log(item, $event);
},
getData() {
this.$vux.loading.show({ text: "Loading" });
var dataset = this;
orderList(this.pageindex, 6, this.userid, this.type)
.then(function(result) {
dataset.$vux.loading.hide();
if (result.data.length > 0) {
dataset.pageindex++;
dataset.title = " " + result.count + " ";
for (var i = 0; i < result.data.length; i++) {
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
} else {
dataset.$vux.toast.show({ text: "" });
}
})
.catch(function(result) {
console.log(result);
dataset.$vux.loading.hide();
alert("");
});
},
CrateItem(order) {
var t = {
desc: "",
meta: { source: "", date: "", other: "" },
data: null
};
t.desc =
"" + order.startlocation + "" + order.endlocation + ".";
t.meta.source = ":" + order.driverprice;
t.meta.date = order.startTime.split("+")[0].replace("T", " ");
t.data = order;
return t;
},
commit(item) {
console.log(item);
var dataset=this;
this.$vux.confirm.show({ value:"", onCancel : () => { console.log(this) }, onConfirm : () => {
dataset.recOrderFun();
} });


},
